*{
	box-sizing: border-box;
}
body {
  margin: 0;
  padding: 0;
  overflow-x: hidden;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

::-webkit-scrollbar {
    width: 4px;
}
::-webkit-scrollbar-thumb {
    background: #cdcdcd;
}

.container{
  width:100vw;
}
#from-to{
  text-align: center;
  color:white;
  height:10vh;
  font-size: 4vh;
}
#from-to,#from,#to{
  background-color: #3d84d5;
}
#from,#to{
  height:100%;
  font-size: 5vh;
  color:white;
  border:0;
  -webkit-appearance:none;
}
textarea{
  background-color: #35a6fd;
  color:white;
  height:45vh;
  font-size: 8vw;
  padding:20px 10px;
  width:100vw;
  font-weight: bold;
}
textarea::-webkit-input-placeholder{
  color:#8bccff;
}
#btn{
  background-color: white;
  color:#35a6ff;
  border:0;
  box-shadow:0 4px 30px;
  width:30vw;
  height:30vw;
  border-radius:50%;
  -webkit-transform:translate(60vw,-20vw);
          transform:translate(60vw,-20vw);
  font-size: 8vw;
}
#result{
  color:#35a6ff;
  margin-top:-76px;
  font-size: 8vw;
  width:100vw;
  font-size: bold;
  padding:0 10px;
  height:45vh;
}
footer{
  font-size: 6vw;
  text-align: center;
  color:#3d84d5;
}
